<template>
  <div class="home">
    <!-- 旋转唱片 -->
    <div class="logo">
      <div class="filter"></div>
      <img alt="Vue logo" src="../assets/logo.png" />
    </div>

    <!-- 播放器 -->
    <audio preload>
      <source :src="songName()[num].addr" type="audio/mp3" />
    </audio>
    <!-- 正则表达式提取歌名 -->
    <p class="songName" style="margin-top: 50px;">{{ songName()[num].addr.match(this.reg)[0] }}</p>
    <div class="tool">
      <div class="previous" @click="previous()"></div>
      <button @click="run()" class="playBtn"></button>
      <div class="nextSong" @click="nextSong()"></div>
      <!-- <HelloWorld msg="Welcome to Your Vue.js App" /> -->
    </div>
    <div class="songList">
      <h2>歌曲库</h2>
      <p v-for="(item,index) in songName()" :key="index" style="margin-top: 20px;">
        <span :value="item.addr.match(this.reg)[0]">{{ item.addr.match(this.reg)[0] }}</span>
      </p>
    </div>
    <!-- 底部扩充 -->
    <div class="buttom">
      
    </div>
  
  </div>
 
</template>

<script>
// @ is an alias to /src
import HelloWorld from "@/components/HelloWorld.vue";
import songs from "@/config/songs";
export default {
  name: "HomeView",
  components: {
    HelloWorld,
  },
  data() {
    return {
      num: 0,
      flagclick: 0,
      reg: /(?<=\/song\/).*?(?=.mp3)/,
    };
  },
  methods: {
    //播放工具给其他函数调用
    runUltil() {
      var audio = document.querySelector("audio");
      var logo = document.querySelector(".logo");
      logo.style.animationPlayState = "running";
      audio.load();
      audio.play();
      //切换图标
      var playBtn = document.querySelector(".playBtn");
      playBtn.style.background = "url(./img/暂停.png) no-repeat";
      playBtn.style.backgroundSize = "50%";
      playBtn.style.backgroundPosition = "50% 50%";
      this.flagclick = 1;
        // 判断播放到哪首歌了 页面还没渲染 设置定时3毫秒后执行
      var songName = document.querySelector(".songName");
      var songlist = document.querySelectorAll(".songList>p>span");
      setTimeout(() => {
        for(var i=0;i<songlist.length;i++){
        if(songlist[i].getAttribute("value")===songName.innerHTML){
          songlist[i].style.color="red"
        }else{
          songlist[i].style.color=""
        }
      }
      }, 300);
    
    },
    // 播放
    run() {
      var songName = document.querySelector(".songName");
      var songlist = document.querySelectorAll(".songList>p>span");
      var logo = document.querySelector(".logo");
      var audio = document.querySelector("audio");
      var playBtn = document.querySelector(".playBtn");
      // 判断播放到哪首歌了
      for(var i=0;i<songlist.length;i++){
        if(songlist[i].getAttribute("value")===songName.innerHTML){
          songlist[i].style.color="red"
        }else{
          songlist[i].style.color=""
        }
      }
      if (this.flagclick % 2 == 0) {
        logo.style.animationPlayState = "running";
        // 唱片碟旋转并且播放
        audio.play();
        this.flagclick++;
        playBtn.style.background = "url(./img/暂停.png) no-repeat";
        playBtn.style.backgroundSize = "50%";
        playBtn.style.backgroundPosition = "50% 50%";
      } else {
        logo.style.animationPlayState = "paused";
        // 唱片碟暂停并且暂停播放
        audio.pause();
        this.flagclick++;
        playBtn.style.background = "url(./img/播放2.png) no-repeat";
        playBtn.style.backgroundSize = "50%";
        playBtn.style.backgroundPosition = "50% 50%";
      }
    },
    // 下一首/([^/]+).mp3$/
    nextSong() {
      if (this.num == this.songName().length - 1) {
        this.num = 0;
        this.runUltil();
      } else {
        this.num++;
        this.runUltil();
      }
    },
    // 上一首
    previous() {
      if (this.num <= 0) {
        this.num = this.songName().length - 1;
        this.runUltil();
      } else {
        this.num--;
        this.runUltil();
      }
    },
    songName() {
      return songs.song;
    },
  },
  mounted(){
 
  },
  computed:{
    
  }
};
</script>
