  height: 300px;
  /* 让他跟图片重叠一起 */
  top: 0;
  left: 0;
  z-index: 7;
  /* 磨砂效果 */
  backdrop-filter: blur(20px);
  background-color: rgba(255, 255, 255, 0.5);
  opacity: 37%;
}
.logo img {
  position: absolute;
  top: 0;
  left: 0;
  height: 300px;
  width: 300px;
}
